import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/Layout/Layout"
import SEO from "../components/SEO/SEO"
import Container from "../components/Container/Container"
import CirclesHero from "../components/CirclesHero/CirclesHero"

export default function TextPage({ data }) {
  const post = data.markdownRemark
  const title = post.frontmatter.title
  const subtitle = post.frontmatter.subtitle
  let featuredImg
  let featuredImgAlt = post.frontmatter.featuredImgAlt || 'Image'

  if (post.frontmatter && post.frontmatter.featuredImage && post.frontmatter.featuredImage.childImageSharp) {
    featuredImg = post.frontmatter.featuredImage.childImageSharp.fluid
  }

  return (
    <Layout>
      <SEO title={title} />
      <CirclesHero title={title} subtitle={subtitle} />
      <Container>
        {featuredImg && <img src={featuredImg.src} alt={featuredImgAlt} />}
        <div dangerouslySetInnerHTML={{ __html: post.html }} />
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        subtitle
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`