import React from 'react'
import styles from './CirclesHero.module.css'
import { graphql, useStaticQuery } from 'gatsby'

const CirclesHero = ({ title, subtitle = null }) => {
    const data = useStaticQuery(graphql`
    query {
        allFile(filter: {
            sourceInstanceName: { eq: "src" }
            relativePath: { eq: "content-management/images/general/circles.png" }
        }) {
            edges {
                node {
                childImageSharp {
                    fluid {
                    ...GatsbyImageSharpFluid
                    }
                }
                }
            }
        }
    }
    `)

    const circleSrc = data.allFile.edges[0].node.childImageSharp.fluid.src;

    return (
        <div className={styles.CirclesHero} style={{
            background: `rgb(106, 115, 218) URL('${circleSrc}') center center/ cover no-repeat`,
            backgroundSize: 'cover',
        }}>
            <div className={styles.Inner}>
                <h1>{title}</h1>
                {subtitle && <h2>{subtitle}</h2>}
            </div>
        </div>
    )
}

export default CirclesHero